footer {
  position: absolute;
  bottom: 0;
  height: auto;
  padding: 5px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  background-color: #f5d100;
  border-top: 2px solid black;
  align-items: center;
}
.contacts {
  display: flex;
}
.contacts li {
  font-size: 1.3rem;
  margin: 10px;
  border: 2px solid black;

  padding: 1px;
}
.mentions-legales {
  width: 60%;
}
