/* POKEMON DISPLAY */

@media screen and (max-width: 900px) {
  .pokemon-display-container {
    padding-bottom: 15vh;
    margin: 50px 0 0 0;
  }
  .pokemon-display-container {
    width: 100%;
  }
}

@media screen and (max-width: 900px) {
  .search-option-container {
    width: 500px;
  }
  .pokemon-card-container {
    grid-template-columns: 470px;
  }
}
@media screen and (max-width: 590px) {
  .search-option-container {
    width: 80%;
  }
  .generation-button-container {
    flex-wrap: wrap;
  }
  .search-input label input {
    width: 70%;
  }
  .pokemon-card-container {
    grid-template-columns: auto;
    margin-top: 30px;
  }
  .pokemon-display-container {
    padding-bottom: 25vh;
  }
}

/* POKEMON CARDS */

@media screen and (max-width: 900px) {
  .pokemon-card {
    width: 450px;
  }
}
@media screen and (max-width: 590px) {
  .pokemon-card {
    width: 80%;
    margin: auto;
  }
}
@media screen and (max-width: 450px) {
  .text-card,
  .type {
    font-size: 0.8rem;
  }
  .type {
    padding: 4px;
  }
  .image-pokemon {
    width: 80px;
  }
}
@media screen and (max-width: 330px) {
  .text-card,
  .type {
    font-size: 0.7rem;
  }
  .type {
    padding: 3px;
  }
  .image-pokemon {
    width: 70px;
  }
}

/* POKEMON DETAILS */

@media screen and (max-width: 930px) {
  .pokemon-details-container {
    padding-top: 20px;
  }
  .evolutions-container {
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 15vh;
  }
  .pokemon-details-card {
    width: 80%;
  }
  .text-details-container {
    font-size: 2.2vw;
  }
  .text-types-details .types-list .type {
    padding: 5px;
    font-size: 2.2vw;
  }
  .pokemon-details-card {
    grid-template-columns: 50% 50%;
  }
  .text-details-container .capture {
    justify-content: space-between;
    width: 80%;
  }
  .stat-pokemon-details {
    width: 80%;
  }
  .nav {
    justify-content: space-around;
  }
  .nav-button,
  .left-button-nav {
    font-size: 5vw;
    position: relative;
    left: 0;
    transform: translateX(0);
  }
  .evolutions-card-container {
    grid-template-columns: auto;
    gap: 15px;
  }
}

/* FOOTER */
@media screen and (max-width: 900px) {
  footer {
    flex-direction: column;
  }
  .mentions-legales {
    width: 90%;
    margin: auto;
  }
  .contacts li {
    font-size: 1.5rem;
  }
}
@media screen and (max-width: 430px) {
  .contacts {
    flex-wrap: wrap;
  }
  .contacts li {
    font-size: 1rem;
  }
  .mentions-legales {
    font-size: 0.8rem;
  }
}
