.pokemon-display-container {
  width: 90%;
  margin: 50px auto 0 auto;
  padding-bottom: 10vh;
  padding-top: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.search-option-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 60%;
  border: 2px solid black;
  padding: 10px;
  background: #d2d1d1;
  text-align: center;
}
.catch-selector {
  width: 150px;
  filter: grayscale(100%);
}
.catch-selector-active {
  width: 150px;
  filter: grayscale(0%);
}
.generation-button-container {
  width: 80%;
  display: flex;
  justify-content: center;
}

.generation-button {
  width: 50px;
  padding: 2px;
  margin: 10px 3px;
  border: 2px solid black;
}
.generation-button:hover {
  cursor: pointer;
}
.selected {
  background: red;
}

.pokemon-card-container {
  display: grid;
  width: 100%;
  margin: 0 auto 0 auto;
  justify-content: center;
  row-gap: 15px;
  grid-template-columns: repeat(auto-fit, 380px);
}
.types-button-container {
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.button-type {
  margin: 0 3px 10px 3px;
  padding: 5px;
  border: 2px solid black;
}
.button-type:hover {
  cursor: pointer;
}

@media not all and (hover: none) and (pointer: coarse) {
  .catch-selector:hover,
  .catch-selector-active:hover {
    animation: catch-selector-anim 1s infinite;
  }
}
@keyframes catch-selector-anim {
  0%,
  100% {
    transform: rotate(0deg);
  }
  15%,
  45%,
  75% {
    transform: rotate(15deg) translateX(5px);
  }
  30%,
  60%,
  90% {
    transform: rotate(-15deg) translateX(-5px);
  }
}

@media screen and (max-width: 900px) {
  .pokemon-display-container {
    padding-bottom: 15vh;
    margin: 50px 0 0 0;
  }
  .pokemon-display-container {
    width: 100%;
  }
}

@media screen and (max-width: 900px) {
  .search-option-container {
    width: 500px;
  }
  .pokemon-card-container {
    grid-template-columns: 470px;
  }
}
@media screen and (max-width: 590px) {
  .search-option-container {
    width: 80%;
  }
  .generation-button-container {
    flex-wrap: wrap;
  }
  .search-input label input {
    width: 70%;
  }
  .pokemon-card-container {
    grid-template-columns: auto;
    margin-top: 30px;
  }
  .pokemon-display-container {
    padding-bottom: 25vh;
  }
}
