.nav {
  position: relative;
  display: flex;
  font-size: clamp(30px, 2vw, 50px);
  padding-top: 10vh;
  margin-top: 30px;
  width: 100%;
}

.left-button-nav {
  position: absolute;
  left: 30px;
}

.exit-button {
  text-align: center;
  margin: 5px;
  border: 6px solid black;
  background: red;
}

.nav-button {
  text-align: center;
  vertical-align: middle;
  margin: 5px;
  border: 6px solid black;
  background: #f5d100;
}

.prev-button {
  position: absolute;
  transform: translateX(-50%);
  left: 40%;
}

.next-button {
  position: absolute;
  transform: translateX(-50%);
  left: 60%;
}
