@import url('https://fonts.googleapis.com/css2?family=Silkscreen&display=swap');

:root {
  --main-color: #f5d100;
  --black-color: #030303;
  --background-color: #f5f4f4;
  --main-font: 'Silkscreen', cursive;
  --fire-color: rgb(238, 62, 62, 1);
  --flying-color: rgb(120, 194, 255, 1);
  --grass-color: rgb(80, 194, 53, 1);
  --poison-color: rgb(174, 101, 225, 1);
  --water-color: rgb(92, 155, 239, 1);
  --bug-color: rgb(174, 189, 62, 1);
  --normal-color: rgb(190, 190, 190, 1);
  --electric-color: rgb(255, 223, 0, 1);
  --ground-color: rgb(173, 109, 61, 1);
  --fairy-color: rgb(245, 143, 242, 1);
  --fighting-color: rgb(245, 137, 63, 1);
  --psychic-color: rgb(248, 61, 141, 1);
  --rock-color: rgb(190, 186, 153, 1);
  --ice-color: rgb(114, 212, 239, 1);
  --dragon-color: rgb(108, 123, 234, 1);
  --steel-color: rgb(124, 181, 201, 1);
  --ghost-color: rgb(123, 82, 122, 1);
  --dark-color: rgb(97, 81, 79, 1);

  --fire-color-full: rgb(230, 34, 36, 1);
  --flying-color-full: rgb(130, 187, 240, 1);
  --grass-color-full: rgb(61, 162, 37, 1);
  --poison-color-full: rgb(147, 63, 205, 1);
  --water-color-full: rgb(35, 129, 240, 1);
  --bug-color-full: rgb(147, 163, 18, 1);
  --normal-color-full: rgb(160, 162, 160, 1);
  --electric-color-full: rgb(251, 195, 0, 1);
  --ground-color-full: rgb(146, 81, 29, 1);
  --fairy-color-full: rgb(241, 112, 239, 1);
  --fighting-color-full: rgb(255, 129, 0, 1);
  --psychic-color-full: rgb(241, 62, 122, 1);
  --rock-color-full: rgb(176, 171, 129, 1);
  --ice-color-full: rgb(61, 216, 254, 1);
  --dragon-color-full: rgb(79, 96, 226, 1);
  --steel-color-full: rgb(94, 162, 186, 1);
  --ghost-color-full: rgb(113, 62, 112, 1);
  --dark-color-full: rgb(79, 62, 60, 1);
}

* {
  list-style: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--main-font);
  color: black;
}

