.pokemon-details-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  padding-bottom: 10vh;
  padding-top: 10vh;
}

.pokemon-details-card {
  width: clamp(800px, 60vw, 1000px);
  display: grid;
  grid-template-columns: 40% 60%;
}

.image-pokemon-details {
  width: 100%;
}

.text-details-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: clamp(1.7rem, 2vw, 2rem);
}

.text-name-details {
  text-align: center;
}
.evolutions-container {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
}

.evolutions-card-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.evolutions {
  display: flex;
  flex-direction: column;
}

.text-details-container ul .type {
  font-size: clamp(1.3rem, 1.5vw, 1.6rem);
}
@media not all and (hover: none) and (pointer: coarse) {
  .image-pokemon-details:hover {
    animation: cardDetailsAnim 3s linear;
  }
}

@keyframes cardDetailsAnim {
  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(2%);
  }
  0%,
  20%,
  40%,
  60%,
  80%,
  100% {
    transform: translateX(0);
  }
}
